import { Component, OnInit, AfterViewInit } from '@angular/core';

@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.scss']
})
export class HomeComponent implements OnInit, AfterViewInit {

  constructor() { }

  ngOnInit() {
  }
  autoScrollWidth(value) {
    while (1 === 1) {

      const element: HTMLDivElement = document.querySelector('.overflow-h');

      if (element.scrollLeft > 523) {
        element.scrollTo({
          left: 0,
          behavior: 'smooth'
        });
      } else {
        const pos = element.scrollLeft;
        element.scrollTo({
          left: pos + value,
          behavior: 'smooth'
        });
      }
    }


  }
  ngAfterViewInit() {




  }


}
