import { ResturantpageComponent } from './components/pages/resturantpage/resturantpage.component';
import { AboutComponent } from './components/pages/about/about.component';
import { ProfileComponent } from './components/pages/profile/profile.component';
import { HomeComponent } from './components/pages/home/home.component';
import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { ResturantComponent } from './components/pages/resturant/resturant.component';

const routes: Routes = [
  { path: '', redirectTo: 'index', pathMatch: 'full' },
  {path: 'index', component: HomeComponent},
  { path: 'resturant', component: ResturantpageComponent},
  {path: 'profile', component: ProfileComponent},
  {path: 'about', component: AboutComponent},

  {path: '**', redirectTo: 'index', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
